import { NEW_RATING } from './types';
import { API, graphqlOperation } from 'aws-amplify';
import { createRating } from '../graphql/mutations';


export const addRating = ratingData => async dispatch => {
    await API.graphql(graphqlOperation(createRating, { input:ratingData} ))
      .then(
        rating =>
          dispatch({
              type: NEW_RATING,
              payload: rating
          })
      ).catch( err => {
          console.log('error',err);
      })
}
