import React, { Component } from "react";
import watched from '../../assets/img/watched.png';
import playBtn from '../../assets/img/play-btn.png';
import SectionEpisodes from "./SectionEpisode";
import Trending from '../../assets/img/trending';
import Carousel from '../../components/Carousel';
import { connect } from 'react-redux';
import { fetchShow, updateShowRating } from '../../actions/showActions';
import { addRating } from '../../actions/ratingActions';
import PropTypes from 'prop-types';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Rating from 'react-rating';
import Icon from '@material-ui/core/Icon';
import {  FacebookIcon,FacebookShareButton } from 'react-share';

var show = {  preview: require ('../../assets/img/video.jpg') };

const trending_images = [
  { image: Trending.image1 },
  { image: Trending.image2 },
  { image: Trending.image3 },
  { image: Trending.image4 },
  { image: Trending.image5 },
  { image: Trending.image6 },
  { image: Trending.image1 },
  { image: Trending.image2 },
  { image: Trending.image3 },
  { image: Trending.image4 },
  { image: Trending.image5 },
  { image: Trending.image6 }
];

function epiSeason1(data,season){
  if (typeof data !== 'undefined'){
      var ep_vars = data.items.filter(episode => episode.id == season);
      return ep_vars[0].episodes.items;
  }else{
    return ep_vars = [];
  }
}

function seasonDrop(data){
      if (typeof data !== 'undefined'){
        var seasonOptions = data.items.map(function(season){
          return <option value={season.id}> Season { season.number }</option>;
        });
      }
  return seasonOptions;
}

const id_gen = require('uuid/v1');

class Show extends Component {

   constructor(){
      super();
      this.state ={
          load: false,
          setOpen: false,
      }
  }

  

  changeEpisode(event) {
      var season = event.target.value;
      var new_section = epiSeason1(this.props.show.seasons,season);
      this.setState({episode: new_section});
  }

  componentDidMount(){
    const { showid } = this.props.match.params;
    this.props.fetchShow( showid );
  }

  openModal(){
      this.setState({ setOpen: true })
  }

  closeModal(){
    this.setState({ setOpen: false })
  }

  clickRating(value){
    var ratingValues = {
      "id": id_gen(),
      "tags": "tags1",
      "userId": this.props.user.username,
      "starRating": value,
      "ratingShowId": this.props.show.id
    }

    var sumRating = (this.props.show.starTotalRatings * this.props.show.starAverageRating) + value;
    var numRating = this.props.show.starTotalRatings + 1;
    var newAverage = sumRating / numRating;
    newAverage = newAverage.toFixed(1);

    var updateShowValues = {
      id: this.props.show.id,
      starTotalRatings: numRating,
      starAverageRating: newAverage
    }
    this.props.addRating(ratingValues);
    this.props.updateShowRating(updateShowValues);
    this.closeModal();
  }

 render() {
  const shareUrl = "https://modshow.tv";
    // let show = this.props.show || {};

   /* const { isLoading, isLoaded, data } = this.props;
   if (!isLoading) return null; // Or whatever you want to return when it is loading

    if ( this.state.load === false ){
      var season_int = show.seasons.items[0].id;
      var ep =  epi_season1(this.props.show.seasons,season_int);
      this.state = { episode: ep };
      this.state.load = true;
    } */

   return (

    <div className="show">
    <div className="container">
		<div className="row top">
			<div className="show-content">
				<h1> { this.props.show.title } </h1>
				<p className="meta-data">
            <span> { this.props.show.year } </span>
            <span> { this.props.show.filmRating } </span>
            <span> { this.props.show.starTotalRatings } Ratings</span>
            <a href="#" className="btn-rate" onClick={this.openModal.bind(this)} > Rate this Show </a>
        </p>
				<p>
            { this.props.show.description }
				</p>
				<table>
					<tr>
						<td> Director </td>
						<td> { this.props.show.director } </td>
					</tr>
					<tr>
						<td> Writer	</td>
						<td className="blue"> { this.props.show.writers } </td>
					</tr>
					<tr>
						<td> Starring	</td>
						<td className="blue">	{ this.props.show.starring } </td>
					</tr>
					<tr>
						<td> Subtitles </td>
						<td> 	{ this.props.show.subtitle } </td>
					</tr>
					<tr>
						<td> Audio Languages </td>
						<td> { this.props.show.audio } </td>
					</tr>
				</table>
			</div>
			<div className="video"> <img src={show.preview} /> </div>
		</div>
    <div className="row">
      <FacebookShareButton children="Share on Facebook" url={shareUrl}>
          <FacebookIcon size={32} round={false} className="fbShare"/> 
          Share on Facebook
        </FacebookShareButton>
    </div>
		 <div className="row">
			<div className="buttons">
				<a href="#" className="btn"> Play Season 1, Episode 1 </a>
				<a href="#" className="btn"> Play Preview </a>
				<a href="#" className="btn"> + Watchlist </a>
			</div>
		</div>
		<div className="episodes">
			<select onChange = {this.changeEpisode.bind(this)}>
          {  seasonDrop(this.props.show.seasons) }
			</select>
          {  <SectionEpisodes data={ this.state.episode } />   }
		</div>
		<div className="watched">

      <div className="animated fadeIn continue">
          <div style={{"padding":"2px 10px","margin":"0 auto"}}>
              	<h3> Customer Also Watched </h3>
                <Carousel children = {trending_images}></Carousel>
          </div>
    </div>
		</div>
    </div>
    <Dialog open={this.state.setOpen} onClose={this.closeModal.bind(this)} aria-labelledby="form-dialog-title">
      <DialogTitle id="form-dialog-title">Rate This Show</DialogTitle>
      <DialogContent>
        <DialogContentText>
          <p> Please click the stars to rate the show </p>
          <Rating
          emptySymbol={<Icon>star_rate</Icon>}
          fullSymbol={<Icon>star_rate</Icon>}
          initialRating={3}
          onClick={this.clickRating.bind(this)}
          />
        </DialogContentText>
      </DialogContent>
    </Dialog>
	</div>
   );
 }
}

Show.propTypes = {
  fetchShow: PropTypes.func.isRequired,
  show: PropTypes.object.isRequired,
  addRating: PropTypes.func.isRequired,
  updateShowRating: PropTypes.func.isRequired,
}

const mapStateToProps = state => ({
  loading: state.shows.isLoading,
  show: state.shows.item.data.getShow
});

export default connect(mapStateToProps, { fetchShow, addRating, updateShowRating })(Show);
