import React from 'react';
import ItemsCarousel from 'react-items-carousel';

class Carousel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activeItemIndex: 0
        }
      }

    render() {
        const children = this.props.children.map( c1 =>
            <div class="slider_item"> <a href="#test"><img src={c1.image} alt="test" /></a></div>
          )
        return (

            <ItemsCarousel
            gutter={12}
            activePosition={'center'}
            chevronWidth={60}
            numberOfCards={6}
            slidesToScroll={2}
            outsideChevron={true}
            showSlither={true}
            freeScrolling={false}
            firstAndLastGutter={false}
            activeItemIndex={this.state.activeItemIndex}
            requestToChangeActive={value => this.setState({ activeItemIndex: value })}
            rightChevron={'>'}
            leftChevron={'<'}
          >
            {children}

          </ItemsCarousel>

        )
    }
}

export default Carousel;
