import React from "react";
import playBtn from '../../assets/img/play-btn.png';

function section_episode(props){
    var section = '';
    if(props.data.length > 0 ){
      section = props.data.map(function(ep_var){
          var balik = <div className="episode">
                        <img src={ep_var.thumbnail} />
                        <div>
                            <h3> { ep_var.title } </h3>
                            <p>{ ep_var.description }</p>
                        </div>
                      </div>
                  return balik;
        });
    }
    return section;
}

export default section_episode
